<template>
  <center>
  <h1>{{message}}</h1>
  <ul class="segmented-control">
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="1" name="option" id="option-1" checked v-on:click="pricestab">
      <label class="segmented-control__label" for="option-1">Prices</label>
    </li>
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="2" name="option" id="option-2" v-on:click="calendarstab">
      <label class="segmented-control__label" for="option-2">Calendars</label>
    </li>
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="3" name="option" id="option-3" v-on:click="newstab">
      <label class="segmented-control__label" for="option-3">News</label>
    </li>
  </ul>


  <div class="usmcontainer" v-show="tab1">
    <div class="usmblock">
      <h2>Focus</h2>
      <div v-for="row in focuses" v-on:click="clickSymbol(row.symbol)">
        <div class="usmpricerow"> <span class="usmcol1"> {{row.symbol}} </span><span class="usmcol2"> {{row.latestPrice}} </span><span class="usmcol3" v-bind:class="{ up : row.changePercent > 0 }"> {{percent(row.changePercent)}} </span> </div>
        <div class="usmnamerow"> {{row.companyName }} </div>
      </div>
    </div>
    <div class="usmblock">
      <h2>Active</h2>
      <div v-for="row in actives" v-on:click="clickSymbol(row.symbol)">
        <div class="usmpricerow"> <span class="usmcol1"> {{row.symbol}} </span><span class="usmcol2"> {{row.latestPrice}} </span><span class="usmcol3" v-bind:class="{ up : row.changePercent > 0 }"> {{percent(row.changePercent)}} </span> </div>
        <div class="usmnamerow"> {{row.companyName }} </div>
     </div>
    </div>
    <div class="usmblock">
      <h2>Gainer</h2>
      <div v-for="row in gainers" v-on:click="clickSymbol(row.symbol)">
        <div class="usmpricerow"> <span class="usmcol1"> {{row.symbol}} </span><span class="usmcol2"> {{row.latestPrice}} </span><span class="usmcol3" v-bind:class="{ up : row.changePercent > 0 }"> {{percent(row.changePercent)}} </span> </div>
        <div class="usmnamerow"> {{row.companyName }} </div>
      </div>
    </div>
    <div class="usmblock">
      <h2>Loser</h2>
      <div v-for="row in losers" v-on:click="clickSymbol(row.symbol)">
        <div class="usmpricerow"> <span class="usmcol1"> {{row.symbol}} </span><span class="usmcol2"> {{row.latestPrice}} </span><span class="usmcol3" v-bind:class="{ up : row.changePercent > 0 }"> {{percent(row.changePercent)}} </span> </div> 
        <div class="usmnamerow"> {{row.companyName }} </div>
      </div>
    </div>
  </div>

  <div class="usmcontainer" v-show="tab2">
    <div v-for="row in ipos">

    </div>
    <div v-for="row in earnings" v-on:click="clickSymbol(row.symbol)">
      <table width="280px">
        <tr>
          <td class="left"><b> {{row.quote.companyName}} </b></td>
          <td class="right"><b> {{row.symbol}} </b></td>
        </tr>
        <tr>
          <td class="left"><b> {{row.EPSReportDate}} </b></td>
          <td class="right"><b> {{row.estimatedEPS}} </b></td>
        </tr>
        <tr>
          <td class="left"> {{row.quote.primaryExchange}} </td>
          <td class="right"> {{percent(row.estimatedChangePercent)}} </td>
        </tr>
        <tr>
          <td class="left"> {{row.fiscalEndDate}} </td>
          <td class="right"> {{row.fiscalPeriod}} </td>
        </tr>
        <tr>
          <td class="left" colspan="2"> {{row.headline}} </td>
        </tr>
      </table>
    </div>
  </div>

  <div v-show="tab3">
    <table><tbody>
      <tr v-for="row in news">
        <td class="news">
          <p class=small>{{row.source}}</p><a :href=row.url class="medium">{{row.headline}}</a><p class="micro">{{timeAgoIex(row.datetime)}}</p>
          <a :href=row.SourceUrl class="small">{{row.summary}}</a>
        </td>
      </tr>
    </tbody></table>

  </div>

  </center>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, Analytics } from 'aws-amplify';
import { timeAgoIex } from '../news-funcs.js';

export default {
  name: 'UsaMarket',
  data() {
    return {
      message: 'USA Stock Market',
      tab1: false,
      tab2: false,
      tab3: false,
      focuses: [],
      actives: [],
      gainers: [],
      losers: [],
      ipos: [],
      earnings: [],
      news: []
    }
  },

  computed: {
    iposShow: function() {
      return this.ipos.length != 0;
    },
    earningsShow: function() {
      return this.earnings.length != 0;
    }
  },


  created() {
    Analytics.record({ name: 'webUsaMarket' });
    this.pricestab();
  },

  methods: {
    pricestab: function() {
      this.tab1 = true;
      this.tab2 = false;
      this.tab3 = false;
      const axios = require('axios');
      axios.get('https://api.iextrading.com/1.0/stock/market/list/infocus')
        .then((response) => {
          this.focuses = response.data;
        })
        .catch((error) => {
        });

      axios.get('https://api.iextrading.com/1.0/stock/market/list/mostactive')
        .then((response) => {
          this.actives = response.data;
        })
        .catch((error) => {
        });

      axios.get('https://api.iextrading.com/1.0/stock/market/list/gainers')
        .then((response) => {
          this.gainers = response.data;
        })
        .catch((error) => {
        });

      axios.get('https://api.iextrading.com/1.0/stock/market/list/losers')
        .then((response) => {
          this.losers = response.data;
        })
        .catch((error) => {
        });

    },

    clickSymbol(symbol) {
      this.$router.push('/stock/' + symbol);
    },

    calendarstab: function() {
      this.tab1 = false;
      this.tab2 = true;
      this.tab3 = false;

      const axios = require('axios');
      axios.get('https://api.iextrading.com/1.0/stock/market/upcoming-ipos')
        .then((response) => {
          this.ipos = response.data.viewData;
        })
        .catch((error) => {
        });

      axios.get('https://api.iextrading.com/1.0/stock/market/today-earnings')
        .then((response) => {
          console.log(response);
          this.earnings = response.data.amc.concat(response.data.bto);
        })
        .catch((error) => {
        });
    },

    newstab: function() {
      this.tab1 = false;
      this.tab2 = false;
      this.tab3 = true;

      const axios = require('axios');
      axios.get('https://api.iextrading.com/1.0/stock/market/news/last/50')
        .then((response) => {
          console.log(response);
          this.news = response.data;
        })
        .catch((error) => {
        });
    },

    percent: function(floatnum) {
      return (parseFloat(floatnum)*100.0).toFixed(2) + "%";
    },

    timeAgoIex: function(datestring) {
      return timeAgoIex(datestring); //call imported function
    }
  }
}
</script>



