<template>
  <center>
  <h1>{{message}}</h1>
  <table><tbody>
    <tr v-for="row in rows">
      <td class="news">
        <img :src=row.ImageUrl style="object-fit: cover; object-position: center;" width="100px" height="100px" align="right"><p class=small>{{row.SourceName}}</p><a :href=row.SourceUrl class="medium">{{row.Title}}</a><p class="micro">{{timeAgo(row.CreateAt)}}</p>
        <a :href=row.SourceUrl class="small">{{row.Detail}}</a>
      </td>
    </tr>
  </tbody></table>
  </center>
</template>

<script>
import Vue from 'vue';
import Amplify, { Auth, API, Analytics } from 'aws-amplify';
import { timeAgo } from '../news-funcs.js';

export default {
  name: 'DoProg',
  data () {
    return {
      message: 'Loading data, please wait seconds',
      rows: [ ]
    }
  },

  async created() {
    //console.log("WWWWWWWWWWWWWWWWWWWWWWWWWWWWwwwwWWWW");
    //const anonymousUser = await Auth.currentCredentials();
    //console.log("XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX");
    //console.log(anonymousUser);
    Analytics.record({ name: 'webDoProg' });

    var arts = localStorage.getItem('arts');
    var after = 0;
    if (arts) {
      this.rows = JSON.parse(arts);
      if (this.rows.length > 0) {
        after = this.rows[0].CreateAt;
      }
    } else {
      this.rows = [];
    }

    API.post('rpv', '/swaListArts', {'body': {'ApiVer': 0, 'After': after, 'Lang': 'en'}}).then(response => {
      this.message = 'Digest of Programmer';
      if (response.Articles.length > 0) {
        this.rows = response.Articles.concat(this.rows);
        localStorage.setItem('arts', JSON.stringify(this.rows));
      }
    }).catch(error => {
      this.message = 'Something went wrong, please retry later';
      console.log(error)
    });
  },

  methods: {
    timeAgo(date) {
      return timeAgo(date);
    }
  }
}

</script>



