<template>
  <div>
    <h1>Welcome to website of Dr. Wu</h1>
    <div class="intr">
      <p>There are multiple applications for you.</p>
      <p><a class="intrlink" v-on:click="comath">CoMath</a>, to help young students succeed in math. 100,000 quizzes of arithmetic, algebra, geometry with solution to help understanding the reasoning behind the formulas, equations, and numbers. <a class="intrlink" v-on:click="comath">Start contest now.</a></p>
      <p><a class="intrlink" v-on:click="doprog">DoProg</a>, picked by programmer, picked for programmers. The newest IT articles, about software architect, startup, entrepreneurship, artificial intelligence (AI), machine learning (ML), blockchain, crypto, augmented reality (AR), virtual reality (VR), may be of interest to software architects and programmers. <a class="intrlink" v-on:click="doprog">I will browse them.</a></p>
      <p><a class="intrlink" v-on:click="sciquiz">SciQuiz</a>, more than 45,000 quizzes about Biology, Chemistry, Geology and Physics to let students practice. <a class="intrlink" v-on:click="sciquiz">Practice immediately.</a></p>
      <p><a class="intrlink" v-on:click="sciquiz">USAMarket</a>, Prices, statistics, company information, financial report, earnings history, dividends and splits events of stocks listed in NYSE and NASDAQ are illustrated for your reference. <a class="intrlink" v-on:click="usamarket">Research from here.</a></p> 
    </div>
  </div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth } from 'aws-amplify';

export default {
  name: 'Home',
  created() {
    Auth.signIn('guest', 'webguest').then(user => {
      Analytics.record({ name: 'webHome' });
    });
  },

  methods: {
    doprog: function() {
        this.$router.push('/doprog')
    },
    comath: function() {
        this.$router.push('/comath')
    },
    sciquiz: function() {
        this.$router.push('/sciquiz')
    },
    usamarket: function() {
    }
  }
}
</script>



