export function replaceImg(source, s3bucket) {
      var index = source.indexOf('{img:');
      if (index == -1) {
        return source;
      }
      var part2string = source.substring(index);
      console.log(part2string);
      var closeIndex = part2string.indexOf('}');
      if (closeIndex == -1) {
        return source;
      }
      var part3string = part2string.substring(closeIndex+1);
      var imgstring = part2string.substring(5, closeIndex);

      var retstring = source.substring(0, index) + '<img src="https://s3.amazonaws.com/' + s3bucket + '/' + imgstring + '">' + part3string;
      return retstring;
}

export function shuffle(a) {
    var j, x, i;
    for (i = a.length - 1; i > 0; i--) {
        j = Math.floor(Math.random() * (i + 1));
        x = a[i];
        a[i] = a[j];
        a[j] = x;
    }
    return a;
}




