
<template>
  <div class="nav">
    <div class="main">
      <a class="item" v-on:click="comath">CoMath</a>
      <a class="item" v-on:click="doprog">DoProg</a>
      <a class="item" v-on:click="sciquiz">SciQuiz</a>
      <a class="item" v-on:click="usamarket">USAMarket</a>
      <a class="item" v-on:click="apps">MobApps</a>
    </div>
    <div class="right">
      <a class="item" v-on:click="profile" v-if="user">Profile</a>
      <amplify-sign-out v-if="user"></amplify-sign-out>
    </div>
  </div>
</template>

<script>
import { Auth, Hub } from 'aws-amplify';
import AmplifyStore from '../store/store';


export default {
  name: 'Menu',
  data () {
    return {}
  },
  computed: {
    user() { 
      return AmplifyStore.state.user
    }
  },
  methods: {
    doprog: function() {
        this.$router.push('/doprog')
    },
    comath: function() {
        this.$router.push('/comath')
    },
    sciquiz: function() {
        this.$router.push('/sciquiz')
    },
    usamarket: function() {
        this.$router.push('/usamarket')
    },
    apps: function() {
        this.$router.push('/apps')
    },
    profile: function() {
        this.$router.push('/profile')
    },
    signOut: function() {
        this.$router.push('/auth/signOut')
    }
  }
}
</script>




