<template>
  <center>
  {{symbol}}

  <ul class="segmented-control">
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="1" name="option" id="option-1" checked v-on:click="charttab">
      <label class="segmented-control__label" for="option-1">Chart</label>
    </li>
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="2" name="option" id="option-2" v-on:click="companytab">
      <label class="segmented-control__label" for="option-2">Company</label>
    </li>
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="3" name="option" id="option-3" v-on:click="newstab">
      <label class="segmented-control__label" for="option-3">News</label>
    </li>
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="4" name="option" id="option-4" v-on:click="peerstab">
      <label class="segmented-control__label" for="option-4">Peers</label>
    </li>
  </ul>

  <div v-show="tab1">
    <line-chart :chart-data="chartdata" :options="options"> </line-chart> 
  </div>

  <div v-show="tab2">
    <p><a :href=company.website>{{company.website}}</a></p>
    <p class="small"> {{company.description}} </p>
    <p class="small"> {{tagsOf(company.tags)}} </p>

    <div class="usmcontainer">
      <div class="usmblock">
        <table width="100%">
          <tr><td class="usmleft">Exchange</td><td class="usmright"> {{company.exchange}} </td></tr>
          <tr><td class="usmleft">Issue type</td><td class="usmright"> {{issueTypeOf(company.issueType)}} </td></tr>
          <tr><td class="usmleft">Industry</td><td class="usmright"> {{company.industry}} </td></tr>
          <tr><td class="usmleft">Sector</td><td class="usmright"> {{company.sector}} </td></tr>
          <tr><td class="usmleft">CEO</td><td class="usmright"> {{company.CEO}} </td></tr>
        </table>
      </div>

      <div class="usmblock">
        <table width="100%">
          <tr><td class="usmleft">Market Cap</td><td class="usmright"> {{stats.marketcap}} </td></tr>
          <tr><td class="usmleft">Float</td><td class="usmright"> {{stats.float}} </td></tr>
          <tr><td class="usmleft">Shares Outstanding</td><td class="usmright"> {{stats.sharesOutstanding}} </td></tr>
          <tr><td class="usmleft">InstitutionPercent</td><td class="usmright"> {{stats.institutionPercent}} </td></tr>
          <tr><td class="usmleft">Insider Percent</td><td class="usmright"> {{stats.insiderPercent}} </td></tr>
        </table>
      </div>
      <div class="usmblock">
        <table width="100%">
          <tr><td class="usmleft">Week52 High</td><td class="usmright"> {{stats.week52high}} </td></tr>
          <tr><td class="usmleft">Week52 Low</td><td class="usmright"> {{stats.week52low}} </td></tr>
          <tr><td class="usmleft">Week52 Change</td><td class="usmright"> {{shortFloat(stats.week52change)}} </td></tr>
          <tr><td class="usmleft">Day200 Moving Avg</td><td class="usmright"> {{shortFloat(stats.day200MovingAvg)}} </td></tr>
          <tr><td class="usmleft">Day50 Moving Avg</td><td class="usmright"> {{shortFloat(stats.day50MovingAvg)}} </td></tr>
        </table>
      </div>
      <div class="usmblock">
        <table width="100%">
          <tr><td class="usmleft">Dividend Rate</td><td class="usmright"> {{stats.dividendRate}} </td></tr>
          <tr><td class="usmleft">Dividend Yield</td><td class="usmright"> {{shortFloat(stats.dividendYield)}} </td></tr>
          <tr><td class="usmleft">Dividend Date</td><td class="usmright"> {{dayOf(stats.exDividendDate)}} </td></tr>
          <tr><td class="usmleft">peRatio High</td><td class="usmright"> {{stats.peRatioHigh}} </td></tr>
          <tr><td class="usmleft">peRatio Low</td><td class="usmright"> {{stats.peRatioLow}} </td></tr>
        </table>
      </div>
      <div class="usmblock">
        <table width="100%">
          <tr><td class="usmleft">Consensus EPS</td><td class="usmright"> {{stats.consensusEPS}} </td></tr>
          <tr><td class="usmleft">Number of Estimates</td><td class="usmright"> {{stats.numberOfEstimates}} </td></tr>
          <tr><td class="usmleft">ttm EPS</td><td class="usmright"> {{shortFloat(stats.ttmEPS)}} </td></tr>
         <tr><td class="usmleft">EPS Surprise Dollar</td><td class="usmright"> {{stats.EPSSurpriseDollar}} </td></tr>
          <tr><td class="usmleft">EPS Surprise Percent</td><td class="usmright"> {{shortFloat(stats.EPSSurprisePercent)}} </td></tr>
         </table>
      </div>
      <div class="usmblock">
        <table width="100%">
          <tr><td class="usmleft">Beta</td><td class="usmright"> {{shortFloat(stats.beta)}} </td></tr>
          <tr><td class="usmleft">EBITDA</td><td class="usmright"> {{stats.EBITDA}} </td></tr>
          <tr><td class="usmleft">Latest EPS</td><td class="usmright"> {{stats.latestEPS}} </td></tr>
          <tr><td class="usmleft">Latest EPS Date</td><td class="usmright"> {{stats.latestEPSDate}} </td></tr>
         </table>
      </div>
      <div class="usmblock">
        <table width="100%">
         <tr><td class="usmleft">Revenue Per Share</td><td class="usmright"> {{stats.revenuePerShare}} </td></tr>
          <tr><td class="usmleft">Revenue Per Employee</td><td class="usmright"> {{stats.revenuePerEmployee}} </td></tr>
          <tr><td class="usmleft">Revenue</td><td class="usmright"> {{stats.revenue}} </td></tr>
          <tr><td class="usmleft">Gross Profit</td><td class="usmright"> {{stats.grossProfit}} </td></tr>
          <tr><td class="usmleft">Cash</td><td class="usmright"> {{stats.cash}} </td></tr>
          <tr><td class="usmleft">Debt</td><td class="usmright"> {{stats.debt}} </td></tr>
        </table>
      </div>

      <div class="usmblock">
        <table width="100%">
          <tr><td class="usmleft">Return on Equity</td><td class="usmright"> {{stats.returnOnEquity}} </td></tr>          <tr><td class="usmleft">Return on Assets</td><td class="usmright"> {{stats.returnOnAssets}} </td></tr>
          <tr><td class="usmleft">Return on Capital</td><td> {{stats.returnOnCapital}} </td></tr>
          <tr><td class="usmleft">Profit Margin</td><td class="usmright"> {{stats.profitMargin}} </td></tr>
          <tr><td class="usmleft">Price to Sales</td><td class="usmright"> {{shortFloat(stats.priceToSales)}} </td></tr>
          <tr><td class="usmleft">Price to Book</td><td class="usmright"> {{stats.PriceToBook}} </td></tr>
         </table>
      </div>
      <div class="usmblock">
        <table width="100%">
          <tr><td class="usmleft">Short interest</td><td class="usmright"> {{stats.shortInterest}} </td></tr>
          <tr><td class="usmleft">Short Date</td><td class="usmright"> {{stats.shortDate}} </td></tr>
          <tr><td class="usmleft">Short Ratio</td><td class="usmright"> {{stats.shortRatio}} </td></tr>
         </table>
      </div>
      <div class="usmblock">
        <table width="100%">
          <tr><td class="usmleft">Year5 Change</td><td class="usmright"> {{percent(stats.year5ChangePercent)}} </td></tr>
          <tr><td class="usmleft">Year2 Change</td><td class="usmright"> {{percent(stats.year2ChangePercent)}} </td></tr>
          <tr><td class="usmleft">Year1 Change</td><td class="usmright"> {{percent(stats.year1ChangePercent)}} </td></tr>
          <tr><td class="usmleft">Ytd Change</td><td class="usmright"> {{percent(stats.ytdChangePercent)}} </td></tr>
          <tr><td class="usmleft">Month6 Change</td><td class="usmright"> {{percent(stats.month6ChangePercent)}} </td></tr>
          <tr><td class="usmleft">Month3 Change</td><td class="usmright"> {{percent(stats.month3ChangePercent)}} </td></tr>
          <tr><td class="usmleft">Month1 Change</td><td class="usmright"> {{percent(stats.month1ChangePercent)}} </td></tr>
          <tr><td class="usmleft">Day5 Change</td><td class="usmright"> {{percent(stats.day5ChangePercent)}} </td></tr>
        </table>
      </div>
    </div>

    <div v-show="fins.length != 0">
      <h2>Financials</h2>
      <table>
      <tr><td class="usmleft">Report Date</td><td v-for="col in fins">{{col.reportDate}}</td></tr>
      <tr><td class="usmleft">Gross Profit</td><td v-for="col in fins">{{col.grossProfit}}</td></tr>
      <tr><td class="usmleft">Cost of Revenue</td><td v-for="col in fins">{{col.costOfRevenue}}</td></tr>
      <tr><td class="usmleft">Operating Revenue</td><td v-for="col in fins">{{col.operatingRevenue}}</td></tr>
      <tr><td class="usmleft">Total Revenue</td><td v-for="col in fins">{{col.totalRevenue}}</td></tr>
      <tr><td class="usmleft">Operating Income</td><td v-for="col in fins">{{col.operatingIncome}}</td></tr>
      <tr><td class="usmleft">Net Income</td><td v-for="col in fins">{{col.netIncome}}</td></tr>
      <tr><td class="usmleft">Research & Development</td><td v-for="col in fins">{{col.researchAndDevelopment}}</td></tr>
      <tr><td class="usmleft">Operating Expense</td><td v-for="col in fins">{{col.operatingExpense}}</td></tr>
      <tr><td class="usmleft">Current Assets</td><td v-for="col in fins">{{col.currentAssets}}</td></tr>
      <tr><td class="usmleft">Total Assets</td><td v-for="col in fins">{{col.totalAssets}}</td></tr>
      <tr><td class="usmleft">Total Liabilities</td><td v-for="col in fins">{{col.totalLiabilities}}</td></tr>
      <tr><td class="usmleft">Current Cash</td><td v-for="col in fins">{{col.currentCash}}</td></tr>
      <tr><td class="usmleft">Current Debt</td><td v-for="col in fins">{{col.currentDebt}}</td></tr>
      <tr><td class="usmleft">Total Cash</td><td v-for="col in fins">{{col.totalCash}}</td></tr>
      <tr><td class="usmleft">Total Debt</td><td v-for="col in fins">{{col.totalDebt}}</td></tr>
      <tr><td class="usmleft">Shareholder Equity</td><td v-for="col in fins">{{col.shareholderEquity}}</td></tr>
      <tr><td class="usmleft">Cash Change</td><td v-for="col in fins">{{col.cashChange}}</td></tr>
      <tr><td class="usmleft">Cash Flow</td><td v-for="col in fins">{{col.cashFlow}}</td></tr>
      <tr><td class="usmleft">Operating Gaings Losses</td><td v-for="col in fins">{{col.operatingGainsLosses}}</td></tr>
      </table>
    </div>

    <div v-show="earnings.length != 0">
      <h2>Earnings</h2>
      <table>
      <tr><td class="usmleft">EPS Report Date</td><td v-for="col in earnings">{{col.EPSReportDate}}</td></tr>
      <tr><td class="usmleft">Fiscal Period</td><td v-for="col in earnings">{{col.fiscalPeriod}}</td></tr>
     <tr><td class="usmleft">Fiscal End Date</td><td v-for="col in earnings">{{col.fiscalEndDate}}</td></tr>
     <tr><td class="usmleft">Announce Time</td><td v-for="col in earnings">{{announceTimeOf(col.announceTime)}}</td></tr>
     <tr><td class="usmleft">Actual EPS</td><td v-for="col in earnings">{{col.actualEPS}}</td></tr>
     <tr><td class="usmleft">Year Ago</td><td v-for="col in earnings">{{col.yearAgo}}</td></tr>
     <tr><td class="usmleft">Year Ago Change</td><td v-for="col in earnings">{{percent(col.yearAgoChangePercent)}}</td></tr>
     <tr><td class="usmleft">Number of Estimates</td><td v-for="col in earnings">{{col.numberOfEstimates}}</td></tr>
     <tr><td class="usmleft">Estimated EPS</td><td v-for="col in earnings">{{col.estimatedEPS}}</td></tr>
     <tr><td class="usmleft">Consensus EPS</td><td v-for="col in earnings">{{col.consensusEPS}}</td></tr>
     <tr><td class="usmleft">EPS Surprise Dollar</td><td v-for="col in earnings">{{col.EPSSurpriseDollar}}</td></tr>
     <tr><td class="usmleft">Estimated Change</td><td v-for="col in earnings">{{col.estimatedChangePercent}}</td></tr>
      </table>
    </div>

    <div v-show="dividends.length != 0">
      <h2>Dividends</h2>
      <table>
      <tr><td class="usmleft">Ex Date</td><td v-for="col in dividends">{{col.exDate}}</td></tr>
      <tr><td class="usmleft">Payment Date</td><td v-for="col in dividends">{{col.paymentDate}}</td></tr>
      <tr><td class="usmleft">Record Date</td><td v-for="col in dividends">{{col.recordDate}}</td></tr>
      <tr><td class="usmleft">Declared Date</td><td v-for="col in dividends">{{col.declaredDate}}</td></tr>
      <tr><td class="usmleft">Amount</td><td v-for="col in dividends">{{col.amount}}</td></tr>
      <tr><td class="usmleft">Type</td><td v-for="col in dividends">{{col.type}}</td></tr>
      <tr><td class="usmleft">Qualified</td><td v-for="col in dividends">{{qualifyOf(col.qualified)}}</td></tr>
      <tr><td class="usmleft">Flag</td><td v-for="col in dividends">{{flagOf(col.flag)}}</td></tr>
      <tr><td class="usmleft">Indicated</td><td v-for="col in dividends">{{col.indicated}}</td></tr>
      </table>
    </div>

    <div v-show="splits.length != 0">
      <h2>Splits</h2>
      <table>
      <tr><td class="usmleft">Ex Date</td><td v-for="col in splits">{{col.exDate}}</td></tr>
      <tr><td class="usmleft">Payment Date</td><td v-for="col in splits">{{col.paymentDate}}</td></tr>
      <tr><td class="usmleft">Record Date</td><td v-for="col in splits">{{col.recordDate}}</td></tr>
      <tr><td class="usmleft">Declared Date</td><td v-for="col in splits">{{col.declaredDate}}</td></tr>
      <tr><td class="usmleft">Ratio</td><td v-for="col in splits">{{col.ratio}}</td></tr>
      <tr><td class="usmleft">To Factor</td><td v-for="col in splits">{{col.toFactor}}</td></tr>
      <tr><td class="usmleft">For Factor</td><td v-for="col in splits">{{col.forFactor}}</td></tr>
      </table>
    </div>

  </div>

  <div v-show="tab3">
   <table><tbody>
      <tr v-for="row in news">
        <td class="news">
          <p class=small>{{row.source}}</p><a :href=row.url class="medium">{{row.headline}}</a><p class="micro">{{timeAgoIex(row.datetime)}}</p>
          <a :href=row.SourceUrl class="small">{{row.summary}}</a>
        </td>
      </tr>
    </tbody></table>
  </div>

  <div class="usmblock" v-show="tab4">
    <div v-for="row in peers" v-on:click="clickSymbol(row.symbol)">
        <div class="usmpricerow"> <span class="usmcol1"> {{row.symbol}} </span><span class="usmcol2"> {{row.latestPrice}} </span><span class="usmcol3" v-bind:class="{ up : row.changePercent > 0 }"> {{percent(row.changePercent)}} </span> </div>
        <div class="usmnamerow"> {{row.companyName }} </div>
      </div>
    </div>
  </div>


  </center>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, Analytics } from 'aws-amplify';
import { timeAgoIex } from '../news-funcs.js';
import VueCharts from 'vue-chartjs'
import { Bar, Line } from 'vue-chartjs'
import LineChart from './LineChart.js'

Vue.component('line-chart', LineChart)

export default {
  name: 'Stock',
  components : { LineChart },

  data() {
    return {
      symbol: '',
      tab1: false,
      tab2: false,
      tab3: false,
      tab4: false,

      loaded: false,
      chartdata: {}, 
      options: {},

      company: {},
      stats: {},
      fins: [],
      earnings: [],
      dividends: [],
      splits: [],

      news: [],

      peers: []
    }
  },


  created() {
    Analytics.record({ name: 'webUsmSymbol' });
    this.symbol = this.$route.params.symbol;

    this.charttab();
  },

  methods: {
    charttab: function() {
      this.tab1 = true;
      this.tab2 = false;
      this.tab3 = false;
      this.tab4 = false;

      this.loaded = false;
      const axios = require('axios');
      axios.get('https://api.iextrading.com/1.0/stock/' + this.symbol + '/chart/1m')
        .then((response) => {
          var labels = [];
          var data = [];
          var tickers = response.data;
          for (var i=0; i< tickers.length; i++) {
            var ticker = tickers[i];
            labels.push(ticker.date);
            data.push(parseFloat(ticker.close));
          }
//console.log(data);
          this.chartdata = { labels: labels,
      datasets: [
        {
          borderColor: '#FC2525',
          pointBackgroundColor: 'white',
          borderWidth: 1,
          pointBorderColor: 'white',
          data: data
        } ]};
          this.options = { responsive: true, maintainAspectRatio: false };
          this.loaded = true;
        });
    },

    companytab: function() {
      this.tab1 = false;
      this.tab2 = true;
      this.tab3 = false;
      this.tab4 = false;
      
      const axios = require('axios');
      axios.get('https://api.iextrading.com/1.0/stock/' + this.symbol + '/company')
        .then((response) => {
          this.company = response.data;
        })
        .catch((error) => {
        });

      axios.get('https://api.iextrading.com/1.0/stock/' + this.symbol + '/stats')
        .then((response) => {
          this.stats = response.data;
        })
        .catch((error) => {
        });

      axios.get('https://api.iextrading.com/1.0/stock/' + this.symbol + '/financials')
        .then((response) => {
          this.fins = response.data.financials;
        })
        .catch((error) => {
        });

      axios.get('https://api.iextrading.com/1.0/stock/' + this.symbol + '/earnings')
        .then((response) => {
          this.earnings = response.data.earnings;
        })
        .catch((error) => {
        });

      axios.get('https://api.iextrading.com/1.0/stock/' + this.symbol + '/dividends/5y')
        .then((response) => {
          this.dividends = response.data;
        })
        .catch((error) => {
        });

      axios.get('https://api.iextrading.com/1.0/stock/' + this.symbol + '/splits/5y')
        .then((response) => {
          this.splits = response.data;
        })
        .catch((error) => {
        });
    },

    newstab: function() {
      this.tab1 = false;
      this.tab2 = false;
      this.tab3 = true;
      this.tab4 = false;

      const axios = require('axios');
      axios.get('https://api.iextrading.com/1.0/stock/' + this.symbol + '/news/last/50')
        .then((response) => {
          this.news = response.data;
        })
        .catch((error) => {
        });
    },

    peerstab: function() {
      this.tab1 = false;
      this.tab2 = false;
      this.tab3 = false;
      this.tab4 = true;

      const axios = require('axios');
      axios.get('https://api.iextrading.com/1.0/stock/' + this.symbol + '/peers')
        .then((response) => {
          var symbols = response.data;
          if (symbols.length == 0) {
            return;
          }

          axios.get('https://api.iextrading.com/1.0/stock/market/batch?types=quote&symbols=' + symbols.join(','))
            .then((response) => {
              this.peers = [];
              var dict = response.data;
              for (var key in dict) {
                this.peers.push(dict[key].quote);
              }
            })
            .catch((error) => {
              console.log(error);
            });
        })
        .catch((error) => {
        });
    },

    percent: function(floatnum) {
      return (parseFloat(floatnum)*100.0).toFixed(2) + "%";
    },

    shortFloat: function(floatnum) {
      return (parseFloat(floatnum)).toFixed(2);
    },

    timeAgoIex: function(datestring) {
      return timeAgoIex(datestring); //call imported function
    },

    dayOf: function(timestring) {
      if (!timestring) {
        return timestring;
      }

      if (timestring.length > 10) {
        return timestring.substring(0, 10);
      }
      return timestring;
    },

    issueTypeOf: function(type) {
        if (type == "ad") {
            return "American Depository Receipt"; // (ADR’s)
        }
        if (type == "re") {
            return "Real Estate Investment Trust"; // (REIT’s)
        }
        if (type == "ce") {
            return "Closed end fund"; // (Stock and Bond Fund)
        }
        if (type == "si") {
            return "Secondary Issue";
        }
        if (type == "lp") {
            return "Limited Partnerships";
        }
        if (type == "cs") {
            return "Common Stock";
        }
        if (type == "et") {
            return "Exchange Traded Fund"; // (ETF)
        }

        return "Not Available"; //, i.e., Warrant, Note, or (non-filing) Closed Ended Funds
    },

    tagsOf: function(ts) {
      if (!ts) {
        return "";
      }
      return ts.join(', ');
    },

    announceTimeOf: function(a) {
        if (a == "BTO") {
            return "Before open";
        }

        if (a == "BTO") {
            return "Before open";
        }

        if (a == "DMT") {
            return "During trading";
        }

        if (a == "AMC") {
            return "After close";
        }
        return a;
    },

    qualifyOf: function(q) {
        if (q == "P") {
            return "Partially qualified income";
        }

        if (q == "Q") {
            return "Qualified income";
        }

        if (q == "N") {
            return "Unqualified income";
        }

        return "N/A";
    },

    flagOf(f) { 
        if (f == "LI") {
            return "Liquidation, instrument liquidates";
        }

        if (f == "PR") {
            return "Proceeds of a sale of rights or shares";
        }

        if (f == "RE") {
            return "Redemption of rights";
        }

        if (f == "AC") {
            return "Accrued dividend";
        }

        if (f == "AR") {
            return "Payment in arrears";
        }

        if (f == "AD") {
            return "Additional payment";
        }

        if (f == "EX") {
            return "Extra payment";
        }

        if (f == "SP") {
            return "Special dividend";
        }

        if (f == "YE") {
            return "Year end";
        }

        if (f == "UR") {
            return "Unknown rate";
        }

        if (f == "SU") {
            return "Regular dividend is suspended";
        }

        return f;
    }
  }
}
</script>



