
<template>
  <center>
  <h1>{{message}}</h1>
  <ul class="segmented-control">
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="1" name="option" id="option-1" checked v-on:click="switchLib(0)">
      <label class="segmented-control__label" for="option-1">{{libnames[0]}}</label>
    </li>
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="2" name="option" id="option-2" v-on:click="switchLib(1)">
      <label class="segmented-control__label" for="option-2">{{libnames[1]}}</label>
    </li>
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="3" name="option" id="option-3" v-on:click="switchLib(2)">
      <label class="segmented-control__label" for="option-3">{{libnames[2]}}</label>
    </li>
   </ul>

  <p class="question" v-html=q></p>
  <div class="support" v-show="answered"><p>{{cs[a]}}</p><p>{{s}}</p>
    <p> <button class="next" v-on:click="loadQuiz()">Next quiz</button> </p>
  </div>
  <div class=choices v-for="(c, index) in cs" :key="index" :class="{'bounce animated': animated}">
    <button v-on:click="choice(index)" v-bind:class="{ 'greenChoice': isGreen(index), 'redChoice': isRed(index), 'blackChoice': isBlack(index)}">{{c}}</button>
  </div>

  </center>
</template>

<script>
import Vue from 'vue';
import Amplify, { API, Analytics } from 'aws-amplify';
import {replaceImg, shuffle} from '../quiz-funcs.js';

export default {
  name: 'CoMath',

  data () {
    return {
      quizname: 'Science Quiz',
      libprefixes : ['e', 'm', 'h'],
      libcounts: [15250, 16236, 13679],
      libnames: ['Elementary', 'Middle', 'High'],
      s3bucket: 'sciquiz',

      message: 'Loading data, please wait seconds',
      libidx: 0,
      q: "",
      cs: [],
      a: 0,
      s: "",
      answered : false,
      choiced: -1,
      animated:false
    }
  },

  async created() {
    //const currentUser = await this.$Amplify.Auth.currentUserInfo();
    Analytics.record({ name: 'webSciQuiz' });
    this.loadQuiz();
  },


  methods: {
    animate: function() {
      var self = this;
      self.animated = true;
      setTimeout(function(){ self.animated = false; }, 1000);
    },

    choice: function(index) {
      if (this.answered) {
        this.animate();        
        return;
      }

      this.choiced = index
      this.answered = true;
    },

    switchLib: function(lib) {
      this.libidx = lib;
      this.loadQuiz();
    },

    loadQuiz: function() {
      var i = this.libprefixes[this.libidx] + (1+Math.floor(Math.random() * this.libcounts[this.libidx]));

      API.post('rpv', '/comQuiz', {'body': {'i': i}}).then(response => {
        this.message = this.quizname;
        this.q = replaceImg(response.Quiz.q, this.s3bucket);
        
        var a = response.Quiz.a;
        var cs;
        if (response.Quiz.d.length > 0) {
          cs = JSON.parse(response.Quiz.d);
          cs.push(a);
          shuffle(cs);
        } else if (response.Quiz.o.length > 0) {
          cs = JSON.parse(response.Quiz.o);
        } else {
          cs = [];
        }

        var index = 0;
        for (var index=0; index<cs.length; index++) {
          var c = cs[index];
          if (a == c) {
            this.a = index;
          }
        }

        this.cs = cs;
        this.s = response.Quiz.s;
        this.answered = false;
        window.scrollTo(0,0);
      }).catch(error => {
        this.message = 'Somethin went wrong, please retry later';
        console.log(error)
      });
    },

    isGreen: function(index) {
      return this.answered && index == this.a;
    },
    isRed: function(index) {
      return this.answered && this.choiced != this.a && this.choiced == index;
    },
    isBlack: function(index) {
      return !this.answered || this.choiced != index && this.a != index;
    }
  }
}

</script>



